	module.exports = {"data":{"markdownRemark":{"html":"<p>When a hacker reports a vulnerability that has already been reported, it's considered a duplicate report. Marking a report as a <em>duplicate</em> enables you to choose from taking 2 options: </p>\n<ul>\n<li>\n<p>Add the second hacker as an external participant on the original report. This means the second hacker will be able to view the contents of the original report. This applies for any subsequent hackers (3rd, 4th, etc.) that submit the same duplicate report and you add them to the original report.</p>\n</li>\n<li>\n<p>Don't add the hacker as an external participant on the original report. Instead, you can let the hacker know that their submitted vulnerability is a duplicate, and you can list the original report number in the comments sections. </p>\n</li>\n</ul>\n<h3>Adding a Hacker to the Original Report</h3>\n<p>To add the hacker to the original report:</p>\n<ol>\n<li>Go to the bottom of the report above the comment box.</li>\n<li>Change the action picker to <strong>Close report > Duplicate</strong>. </li>\n<li>Enter the original report number in the <strong>Search report</strong> field and select the report from the selection list. </li>\n<li>A check box stating <strong>Add <em>hacker name</em> to the original report</strong> will show under the <strong>Search report</strong> field. </li>\n<li>Select the check box to add the hacker to the report. </li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/duplicate-reports-086fb5ff8020df72ca143b30abe3e5be-088b5.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 36.71373555840822%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"duplicate reports\"\n        title=\"\"\n        src=\"/static/duplicate-reports-086fb5ff8020df72ca143b30abe3e5be-2f183.png\"\n        srcset=\"/static/duplicate-reports-086fb5ff8020df72ca143b30abe3e5be-67829.png 125w,\n/static/duplicate-reports-086fb5ff8020df72ca143b30abe3e5be-5c59f.png 250w,\n/static/duplicate-reports-086fb5ff8020df72ca143b30abe3e5be-2f183.png 500w,\n/static/duplicate-reports-086fb5ff8020df72ca143b30abe3e5be-c93e8.png 750w,\n/static/duplicate-reports-086fb5ff8020df72ca143b30abe3e5be-4e628.png 1000w,\n/static/duplicate-reports-086fb5ff8020df72ca143b30abe3e5be-b3ac4.png 1500w,\n/static/duplicate-reports-086fb5ff8020df72ca143b30abe3e5be-088b5.png 1558w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>It's up to your program to determine if you are comfortable with sharing the original report with the hacker that submitted the duplicate report. It's recommended to tie the second report to the original report to provide accountability and to account for Reputation gain or loss for the hacker. </p>","frontmatter":{"path":"/programs/duplicate-reports.html","id":"programs/duplicate-reports","title":"Duplicate Reports","description":null}}},"pathContext":{}}

